import React, { Component } from "react";
import {
  Breadcrumb,
  BreadcrumbItem,
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  Col,
  Row,
  FormFeedback,
} from "reactstrap";
import { Link, withRouter } from "react-router-dom";

class Employee extends Component {
  constructor(props) {
    super(props);
    this.state = {
      empname: "",
      telnum: "",
      email: "",
      message: "",
      empid: "",
      dept: "",
      hod: "",
      touched: {
        empname: false,
        telnum: false,
        email: false,
      },
    };
  }

  handleBlur = (field) => (evt) => {
    this.setState({
      touched: { ...this.state.touched, [field]: true },
    });
  };
  handleInputChange = (event) => {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    this.setState({ [name]: value });
  };

  validate = (empname, telnum, email) => {
    const errors = {
      empname: "",
      telnum: "",
      email: "",
    };

    if (this.state.touched.empname && empname.length < 3)
      errors.empname = "Employee Name should be >= 3 characters";
    else if (this.state.touched.empname && empname.length > 30)
      errors.firstname = "Employee Name should be <= 30 characters";

    const reg = /^\d+$/;
    if (this.state.touched.telnum && !reg.test(telnum))
      errors.telnum = "Tel. Number should contain only numbers";

    if (
      this.state.touched.email &&
      email.split("").filter((x) => x === "@").length != 1
    )
      errors.email = "Email should contain a @";
    return errors;
  };

  handleSubmit = (event) => {
    let path = "/home";
    this.props.withRouter.push(path);
    event.preventDefault();
  };

  render() {
    const errors = this.validate(
      this.state.empname,
      this.state.telnum,
      this.state.email
    );
    return (
      <React.Fragment>
        <div className="container-fluid">
          <div className="row">
            <Breadcrumb>
              <BreadcrumbItem>
                <Link to="/home">Home</Link>
              </BreadcrumbItem>
              <BreadcrumbItem active>Contact Us</BreadcrumbItem>
            </Breadcrumb>
            <div className="col-12">
              <h3>Employee</h3> <hr />
            </div>
          </div>
        </div>

        <div className="container-fluid">
          <div className="row row-content">
            <div className="col-12 col-sm-8 m-2 app-wrap">
              <h2 className="homeTitle center">Leave Application</h2>

              <Form
                onSubmit={this.handleSubmit}
                onChange={this.handleInputChange}
              >
                <FormGroup row>
                  <Label htmlFor="empid" md={2}>
                    Employee Id{" "}
                  </Label>
                  <Col md={10}>
                    <Input
                      type="text"
                      id="empid"
                      name="empid"
                      placeholder="Employee Id"
                      value={this.state.empid}
                    />
                  </Col>
                </FormGroup>

                <FormGroup row>
                  <Label htmlFor="empname" md={2}>
                    Employee Name
                  </Label>
                  <Col md={10}>
                    <Input
                      type="text"
                      id="empname"
                      name="empname"
                      placeholder="Employee Full Name"
                      value={this.state.empname}
                      valid={errors.empname === ""}
                      invalid={errors.empname !== ""}
                      onBlur={this.handleBlur("empname")}
                      required
                    />
                    <FormFeedback>{errors.empname}</FormFeedback>
                  </Col>
                </FormGroup>

                <FormGroup row>
                  <Label htmlFor="dept" md={2}>
                    Department{" "}
                  </Label>
                  <Col md={10}>
                    <Input
                      type="dept"
                      id="dept"
                      name="dept"
                      placeholder="Enter Department name"
                      value={this.state.dept}
                    />
                  </Col>
                </FormGroup>

                <FormGroup row>
                  <Label htmlFor="hod" md={2}>
                    Head of Department{" "}
                  </Label>
                  <Col md={10}>
                    <Input
                      type="text"
                      id="hod"
                      name="hod"
                      placeholder="Head of Department"
                      value={this.state.hod}
                    />
                  </Col>
                </FormGroup>

                <FormGroup row>
                  <Label htmlFor="telnum" md={2}>
                    Telephone Number
                  </Label>
                  <Col md={10}>
                    <Input
                      type="text"
                      id="telnum"
                      name="telnum"
                      placeholder="Telephone Number"
                      value={this.state.telnum}
                      valid={errors.telnum === ""}
                      invalid={errors.telnum !== ""}
                      onBlur={this.handleBlur("telnum")}
                    />
                    <FormFeedback>{errors.telnum}</FormFeedback>
                  </Col>
                </FormGroup>

                <FormGroup row>
                  <Label htmlFor="email" md={2}>
                    Email
                  </Label>
                  <Col md={10}>
                    <Input
                      type="email"
                      id="email"
                      name="email"
                      placeholder="Email"
                      value={this.state.email}
                      valid={errors.email === ""}
                      invalid={errors.email !== ""}
                      onBlur={this.handleBlur("email")}
                    />
                    <FormFeedback>{errors.email}</FormFeedback>
                  </Col>
                </FormGroup>

                <FormGroup row>
                  <Label htmlFor="message" md={2}>
                    Reason of Leave :
                  </Label>
                  <Col md={10}>
                    <Input
                      type="textarea"
                      id="message"
                      name="message"
                      rows="12"
                      value={this.state.message}
                    />
                  </Col>
                </FormGroup>

                <FormGroup row>
                  <Col md={{size:10, offset:2}}>

                  <Button type="reset" size="lg" color="dark" className="mr-2">
                      Cancel
                    </Button>
                    
                    <Button type="submit" size="lg" color="primary" className="mr-2">
                      submit
                    </Button>
                  </Col>
                </FormGroup>
              </Form>
            </div>

            <div className="col-12 col-sm-3 m-2 settings-wrap">
              <h2 className="homeTitle center">Settings</h2>
              <div className="settings-wrap settingsBtn">
                <button type="button" class="btn btn-outline-light mb-3 mt-3">
                  My Profile
                </button>

                <button type="button" class="btn btn-light mb-3">
                  Application
                </button>

                <button type="button" class="btn btn-outline-light mb-3">
                  Leave Records
                </button>

                <button type="button" class="btn btn-outline-light mb-3">
                  Track My Leave
                </button>

                <button type="button" class="btn btn-dark mb-3">
                  Logout
                </button>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Employee;
