import React from 'react';
import { Breadcrumb, BreadcrumbItem, Card, CardBody, CardHeader, Media } from 'reactstrap';
import { Link } from 'react-router-dom';


const RenderLeader=(props)=>
{
    return(
    <Media tag="li">
        <Media left middle>
        <Media object src={props.leader.image} alt={props.leader.name} width="82" height="82"/>
        </Media>

        <Media body className="ml-5">
            <Media heading>{props.leader.name}</Media>
            <p>{props.leader.designation}</p>
            <p>{props.leader.description}</p>
        </Media>
    </Media>
    );
}

function About(props) {

    const leaders = props.leaders.map((leader) => {
        return (
           <React.Fragment> <RenderLeader leader={leader}/><br /></React.Fragment>
        );
    });

    return(
        <div className="container">
            <div className="row">
                <Breadcrumb>
                    <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                    <BreadcrumbItem active>About Us</BreadcrumbItem>
                </Breadcrumb>
                <div className="col-12">
                    <h3>About Us</h3>
                    <hr />
                </div>                
            </div>
            <div className="row row-content">
                <div className="col-12 col-md-6">
                    <h2>Our History</h2>
                    <p>Started in 2010, Leave Application Manganement System quickly established itself as a Management icon par excellence globally. With its unique brand , it enjoys patronage from the A-list clientele in Hong Kong. With feature rich Management System like LAMS, you won't want any other system .</p>                  
                </div>
                <div className="col-12 col-md-5">
                    <Card>
                        <CardHeader className="bg-primary text-white">Facts At a Glance</CardHeader>
                        <CardBody>
                            <dl className="row p-1">
                                <dt className="col-6">Started</dt>
                                <dd className="col-6">3 Feb. 2013</dd>
                                <dt className="col-6">Major Stake Holder</dt>
                                <dd className="col-6">HK Fine Foods Inc.</dd>
                                <dt className="col-6">Last Year's Turnover</dt>
                                <dd className="col-6">$1,250,375</dd>
                                <dt className="col-6">Employees</dt>
                                <dd className="col-6">40</dd>
                            </dl>
                        </CardBody>
                    </Card>
                </div>
            </div>
            <div className="row row-content">
                <div className="col-12">
                    <h2>Corporate Leadership</h2>
                </div>
                <div className="col-12">
                    <Media list>
                        {leaders}
                    </Media>
                </div>
            </div>
        </div>
    );
}



export default About; 